<template>
  <div class="flex" v-if="isSupported">
    <button @click="open">
      Получить цвет
    </button>
    <div>RGB Hex: {{ sRGBHex }}</div>
    <div>RGB Dec: rgb({{ decimalRGB }})</div>
    <div :style="{backgroundColor: sRGBHex}" class="box"></div>
  </div>
  <div v-else>Your browser is not supported</div>
</template>

<script>
import {useEyeDropper} from "@vueuse/core";
export default {
  name: 'App',
  setup() {
    const {isSupported, sRGBHex, open} = useEyeDropper()
    return {isSupported, sRGBHex, open}
  },
  computed: {
    decimalRGB() {
      const hex = this.sRGBHex.slice(1, this.sRGBHex.length)
      console.log(hex)
      let bigint = parseInt(hex, 16);
      let r = (bigint >> 16) & 255;
      let g = (bigint >> 8) & 255;
      let b = bigint & 255;
      return r + "," + g + "," + b;
    }
  }
}
</script>

